@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.navbar{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 70px;
    color: #fff;
    background-color: mediumvioletred;

}
.nav-links{
    display: flex;
    justify-content: flex-end;
    list-style: none;
    width: 75%;
}
.home,.about,.contact,.services,.gallery,.signin{
    text-decoration: none;
    color: #fff;
    font-size: 20px;
    padding:10px 20px;
    margin: 0 10px;
    cursor: pointer;

}
.signin{
    border: 1px solid white;
    border-radius: 10px;
}
.nav-menu-icon{
    display: none;
}
@media screen and (max-width:780px) {
    .logo{
        display: flex;
        position: absolute;
        top: 15px;
        left: 35px;

    }
    .nav-links{
        display: none;

    }
    .nav-link-mob{
        display: block;
        position: absolute;
        list-style: none;
        background-color: #ff07ff;
        left: 0;
        top: 70px;
        transition: 0.5S all ease-out;
        width: 100%;


    }
    .home,.about,.contact,.services,.gallery,.signin{
        color: #fff;
        text-align: center;
        padding: 32px;
        width: 100%;
        transition: 0.5S all ease;

    }
    .signin{
        border: none;
    }
    .nav-menu-icon{
        display: block;
        position: absolute;
        font-size: 30px;
        color: #fff;
        background-color: mediumvioletred;
        top: 15px;
        right: 25px;
        border: none;

    }
}